import React from 'react';
import { Typography } from 'antd';

const { Title, Text } = Typography;

const WelcomeCard: React.FC = () => (
  <div>
    <Title level={2}>Learn English!</Title>
    <Title level={4}>Приложение для изучения времен английского языка</Title>
    <Text>
      Всем известно, что английский широко распространен во всем мире, поэтому, зная этот язык, вы
      сможете спокойно общаться с людьми из различных стран. Это позволит вам заводить полезные
      знакомства, разговаривать и переписываться с иностранными друзьями, узнавать много новой и
      интересной информации.
    </Text>
  </div>
);

export default WelcomeCard;
